<template>
  <div>
    <div class='userme'>
      <div class='myInfo'>{{$t('User.UserMe')}}</div>
      <div class='avatar infoItem'>
        <el-avatar :size="100" :src="avatar" shape="square"></el-avatar>
        <el-button class='edit' type='primary' @click='newAvatar'>{{$t('User.Upload')}}</el-button>
      </div>

      <div class='infos'>
        <div class='infoItem'>
          <span class='infoLeft'>{{$t('User.UserID')}}</span>
          <span class='infoRight uneditable'>{{userid}}</span>
        </div>

        <div class='infoItem'>
          <span class='infoLeft'>{{$t('User.Nickname')}}</span>
          <span class='infoRight editable' @click='changeNickname'>{{nickname}}</span>
        </div>

        <div class='infoItem'>
          <span class='infoLeft'>{{$t('User.Email')}}</span>
          <span class='infoRight uneditable'>{{email}}</span>
        </div>

        <div class='infoItem'>
          <span class='infoLeft'>{{$t('User.RealName')}}</span>
          <span class='infoRight uneditable'>{{realname}}</span>
        </div>

        <div class='infoItem'>
          <span class='infoLeft'>{{$t('User.StudentID')}}</span>
          <span class='infoRight uneditable'>{{studentid}}</span>
        </div>

        <div class='infoItem'>
          <span class='infoLeft'>{{$t('User.School')}}</span>
          <span class='infoRight uneditable'>{{school}}</span>
        </div>

        <div class='infoItem'>
          <span class='infoLeft'>{{$t('User.Password')}}</span>
          <span class='infoRight editable' @click='changePassword'>{{password}}</span>
        </div>

        <div class='infoItem'>
          <span class='infoLeft'>{{$t('User.Auth')}}</span>
          <span class='infoRight editable' v-if='authAllowed' @click='changeAuth'>{{userType}}</span>
          <span class='infoRight uneditable' v-if='!authAllowed'>{{userType}}</span>
        </div>

        <div class='infoItem'>
          <span class='infoLeft'>{{$t('User.Purse')}}</span>
          <span class='infoRight editable' @click='changePurse'>￥{{purse}}</span>
        </div>
      </div>

      <div class='buttons'>
        <el-button class='aButton' type='danger' @click='logout'>{{$t('User.Logout')}}</el-button>
      </div>
    </div>

    <el-dialog class='dialog' :title='this.$t("User.EdittingInfo")' :visible.sync='isEditting' append-to-body
      width='80%'>
      <div>
        <div class='changeNickname' v-show='edittingItem == "nickname"'>
          <el-input class='inputBox' v-model='newNickname' :placeholder='$t("User.NewNickname")'></el-input>
        </div>
        <div class='changePassword' v-show='edittingItem == "password"'>
          <el-input class='inputBox' v-model='oldPassword' :placeholder='$t("User.OldPassword")'></el-input>
          <el-input class='inputBox' v-model='newPassword' :placeholder='$t("User.NewPassword")'></el-input>
          <el-input class='inputBox' v-model='newPasswordConfirm'
            :placeholder='$t("User.NewPasswordConfirm")'></el-input>
        </div>
        <div class='changeAuth' v-show='edittingItem == "auth"'>
          <div class='select'>
            <span>{{$t("User.AuthType")}}</span>
            <el-select class='selectBox' v-model="authType">
              <el-option :label='$t("User.AuthTypePerson")' value='1' disabled></el-option>
              <el-option :label='$t("User.AuthType2")' value='2'></el-option>
              <el-option :label='$t("User.AuthType3")' value='3'></el-option>
            </el-select>
          </div>

          <el-input class='inputBox' v-model='authName' :placeholder="$t('User.AuthName')"></el-input>
        </div>
      </div>

      <div class='edittingFooter' slot='footer'>
        <el-button @click="isEditting = false">{{$t('System.Cancel')}}</el-button>
        <el-button type="primary" @click="confirmEditting()">{{$t('System.Confirm')}}</el-button>
      </div>
    </el-dialog>

    <el-dialog class='doingPurse' :title='this.$t("User.Purse")' :visible.sync='doingPurse' append-to-body>
      <el-tabs>
        <el-tab-pane :label="this.$t('User.PurseTake')">
          <el-input class='input' :placeholder='$t("User.PurseTakeAmount")' v-model='pursetake' type='number'
            :max='this.purse' @input='calTakePurse'></el-input>
          <div class='label'>{{$t('User.PurseTakeGet')}}: {{pursetakereal}}</div>
          <div class='button'>
            <el-button type='primary' @click='purseCheck(0)'>{{$t('User.PurseTakeGo')}}</el-button>
          </div>
        </el-tab-pane>

        <el-tab-pane :label="this.$t('User.PurseAdd')">
          <el-input class='input' :placeholder='$t("User.PurseAdd")' v-model='purseadd' type='number'
            :max='500'></el-input>
          <div class='button'>
            <el-button type='primary' @click='purseCheck(1)'>{{$t('User.PurseAddGo')}}</el-button>
          </div>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>

    <el-dialog class='avatarUploader' :title='this.$t("User.AvatarTitle")' :visible.sync='uploadingAvatar'
      append-to-body width='80%'>
      <el-upload class="uploader" action="server.default.uploadImg" :show-file-list="false" :http-request="uploadAvatar"
        :before-upload="beforeAvatarUpload">
        <img v-if='uploadingAvatarUrl' :src='uploadingAvatarUrl' class="avatarImage" />
        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
      </el-upload>
      <div class='avatarWarn'>{{this.$t('User.AvatarWarn')}}</div>
    </el-dialog>
  </div>
</template>

<script>
import axios from 'axios'
import Cookies from 'js-cookie'
import * as server from '../../../server.js'
import md5 from 'js-md5'
import { customAlphabet } from 'nanoid';
const nanoid = customAlphabet('1234567890', 9);

export default {
  name: 'user-me',
  data () {
    return {
      isEditting: false,
      edittingItem: '',
      doingPurse: false,
      newNickname: '',
      newPassword: '',
      newPasswordConfirm: '',
      oldPassword: '',
      authAllowed: false,
      authType: '',
      authName: '',
      avatar: '',
      userid: '',
      nickname: '',
      email: '',
      realname: '',
      studentid: '',
      school: '',
      password: '********',
      userType: '',
      purse: 0,
      pursetake: 0,
      pursetakereal: 0,
      purseadd: 0,
      uploadingAvatar: false,
      uploadingAvatarUrl: ''
    }
  },
  methods: {
    uploadAvatar (file) {
      const loadingInstance = this.loading();

      let formData = new FormData();
      formData.append("file", file.file);
      formData.append("type", "user");
      formData.append("cookie", Cookies.get('loginAs'))

      axios({
        method: "post",
        url: server.default.uploadImg,
        data: formData
      }).then((response) => {
        loadingInstance.close();

        if (response.status != 200) {
          this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
        }
        else {
          if (response.data == 'IMAGE_UPLOAD_SUCCESS') {
            this.alert(this.$t('User.AvatarSuccess'), this.$t('System.Info'), () => {
              this.redirect('userMe')
            })
          }
        }
      });
    },
    beforeAvatarUpload (file) {
      if (file.type != 'image/jpeg' && file.type != 'image/png') {
        this.alert(this.$t('User.AvatarWrongType'), this.$t('System.Warning'));
        return false;
      }
      else if (file.size / 1024 / 1024 > 2) {
        this.alert(this.$t('User.AvatarTooLarge'), this.$t('System.Warning'));
        return false;
      }
      else {
        return true;
      }
    },
    changeNickname () {
      this.isEditting = true;
      this.edittingItem = 'nickname'
    },
    changePassword () {
      this.isEditting = true;
      this.edittingItem = 'password'
    },
    changeAuth () {
      this.isEditting = true;
      this.edittingItem = 'auth'
    },
    confirmEditting () {
      if (this.edittingItem == 'nickname') {
        if (this.newNickname.length < 3 || this.newNickname.length > 10) {
          this.alert(this.$t("User.NewNicknameBad"), this.$t("System.Warning"))
        }
        else {
          const loadingInstance = this.loading();
          axios({
            method: 'post',
            url: server.default.changeUsername,
            data: new URLSearchParams({
              'nickname': this.newNickname,
              'cookie': Cookies.get('loginAs')
            })
          }).then((response) => {
            loadingInstance.close();
            if (response.status != 200) {
              this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
            }
            else {
              if (response.data == 'UPDATE_USERNAME_TAKEN') {
                this.alert(this.$t('User.NewNicknameTaken'), this.$t('System.Warning'))
              }
              else if (response.data == 'UPDATE_USERNAME_OK') {
                this.alert(this.$t('User.NewInfoComplete'), this.$t('System.Info'), () => {
                  Cookies.remove('loginAs');
                  Cookies.remove('loginCookie');
                  location.href = location.origin;
                });
              }
              else {
                this.alert('Unexpected Response!', this.$t('System.Error'))
              }
            }
          })
        }
      }

      else if (this.edittingItem == 'password') {
        if (this.newPassword.length < 8 || this.newPassword.length > 16) {
          this.alert(this.$t("User.NewPasswordBad"), this.$t("System.Error"))
        }
        else if (this.newPassword != this.newPasswordConfirm) {
          this.alert(this.$t("User.NewPasswordWrong"), this.$t("System.Error"))
        }
        else {
          const loadingInstance = this.loading();
          axios({
            method: 'post',
            url: server.default.changePwd,
            data: new URLSearchParams({
              'oldPassword': md5(this.oldPassword),
              'password': md5(this.newPasswordConfirm),
              'cookie': Cookies.get('loginAs')
            })
          }).then((response) => {
            loadingInstance.close();
            if (response.status != 200) {
              this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
            }
            else {
              if (response.data == 'UPDATE_PASSWORD_NOMATCH') {
                this.alert(this.$t('User.OldPasswordWrong'), this.$t('System.Warning'))
              }
              else if (response.data == 'UPDATE_PASSWORD_OK') {
                this.alert(this.$t('User.NewInfoComplete'), this.$t('System.Info'), () => {
                  Cookies.remove('loginAs');
                  Cookies.remove('loginCookie');
                  location.href = location.origin;
                });
              }
              else {
                this.alert('Unexpected Response!', this.$t('System.Error'))
              }
            }
          })
        }
      }

      else if (this.edittingItem == 'auth') {
        if (this.authType == '' || this.authName == '') {
          this.alert(this.$t("User.AuthInputBad"), this.$t("System.Error"))
        }
        else {
          const loadingInstance = this.loading();
          axios({
            method: 'post',
            url: server.default.newAdminWork,
            data: new URLSearchParams({
              'workid': nanoid(),
              'type': 1,
              'cookie': Cookies.get('loginAs'),
              'authtype': this.authType,
              'content': this.authName
            })
          }).then((response) => {
            loadingInstance.close();
            if (response.status != 200) {
              this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
            }
            else {
              if (response.data == 'ADMINWORK_WORKID_TAKEN') {
                this.confirmEditting();
                return;
              }
              else if (response.data == 'ADMINWORK_NEW_COMPLETE') {
                this.alert(this.$t('User.AuthSubmitted'), this.$t('System.Info'))
              }
              else {
                this.alert('Unexpected Response!', this.$t('System.Error'))
              }
            }
          })
        }
      }
      this.isEditting = false
    },
    logout () {
      this.$confirm(this.$t('User.LogoutConfirm'), this.$t('System.Warning'), {
        confirmButtonText: this.$t('System.Yes'),
        cancelButtonText: this.$t('System.No'),
        type: 'warning'
      }).then(() => {
        Cookies.remove('loginAs');
        Cookies.remove('loginCookie');
        location.href = location.origin;
      }).catch(() => { });
    },
    getUserInfo () {
      const loadingInstance = this.loading();

      axios({
        method: 'post',
        url: server.default.user,
        data: new URLSearchParams({
          'type': 'me',
          'cookie': Cookies.get('loginAs')
        })
      }).then((response) => {
        loadingInstance.close();
        if (response.status != 200) {
          this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
        }
        else {
          if (response.data == 'USER_NOMATCH') {
            this.alert(this.$t('System.IllegalRequest'), this.$t('System.Error'));
          }
          else if (response.data.status == 'USER_FOUND') {
            this.userid = response.data.userid;
            this.nickname = response.data.username;
            this.email = response.data.email;
            this.realname = response.data.realname;
            this.studentid = response.data.studentid;
            this.school = this.$t("User.UserSchool" + response.data.school);
            this.userType = this.formatUserType(response.data.userstatus, response.data.authname);
            this.avatar = this.getUserAvatar(this.userid)
            if (response.data.userstatus == 1) {
              this.authAllowed = true;
            }
            else {
              this.authAllowed = false;
            }
          }
          else {
            this.alert('Unexpected Response!', this.$t('System.Error'));
          }
        }
      });
    },
    getPurse () {
      const loadingInstance = this.loading();

      axios({
        method: 'post',
        url: server.default.getPurse,
        data: new URLSearchParams({
          'cookie': Cookies.get('loginAs')
        })
      }).then((response) => {
        loadingInstance.close();
        if (response.status != 200) {
          this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
        }
        else {
          if (response.data == 'USER_NOMATCH') {
            this.alert(this.$t('System.IllegalRequest'), this.$t('System.Error'));
          }
          else if (response.data.status == 'TRADE_GETPURSE_COMPLETE') {
            this.purse = response.data.purse.toFixed(2);
          }
          else {
            this.alert('Unexpected Response!', this.$t('System.Error'));
          }
        }
      });
    },
    calTakePurse () {
      this.pursetakereal = (0.98 * this.pursetake).toFixed(2);
    },
    changePurse () {
      this.doingPurse = true;
    },
    purseCheck (type) {
      if (type == 0) {
        if (parseFloat(this.pursetakereal) > parseFloat(this.purse)) {
          this.alert(this.$('User.PurseNotEnough'), this.$t('System.Warning'));
        }
        else if (this.pursetakereal < 0.98) {
          this.alert(this.$('User.PurseTakeFew'), this.$t('System.Warning'));
        }
        else {
          const loadingInstance = this.loading();

          axios({
            method: 'post',
            url: server.default.takePurse,
            data: new URLSearchParams({
              'cookie': Cookies.get('loginAs'),
              'amount': this.pursetakereal
            })
          }).then((response) => {
            loadingInstance.close();
            if (response.status != 200) {
              this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
            }
            else {
              if (response.data == 'USER_NOMATCH') {
                this.alert(this.$t('System.IllegalRequest'), this.$t('System.Error'));
              }
              else if (response.data.status == 'TRADE_TAKEPURSE_LEAK') {
                this.getPurse();
                this.doingPurse = false;
                this.alert(this.$('User.PurseNotEnough'), this.$t('System.Warning'));
              }
              else if (response.data.status == 'TRADE_TAKEPURSE_COMPLETE') {
                this.getPurse();
                this.doingPurse = false;
                this.alert(this.$('User.PurseTakeComplete'), this.$t('System.Info'));
              }
              else {
                this.alert('Unexpected Response!', this.$t('System.Error'));
              }
            }
          });
        }
      }
      else if (type == 1) {
        if (this.purseadd > 500) {
          this.alert(this.$('User.PurseAddMuch'), this.$t('System.Warning'));
        }
        else {
          const loadingInstance = this.loading();

          axios({
            method: 'post',
            url: server.default.addPurse,
            data: new URLSearchParams({
              'cookie': Cookies.get('loginAs'),
              'amount': this.purseadd
            })
          }).then((response) => {
            loadingInstance.close();
            if (response.status != 200) {
              this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
            }
            else {
              if (response.data == 'USER_NOMATCH') {
                this.alert(this.$t('System.IllegalRequest'), this.$t('System.Error'));
              }
              else if (response.data.status == 'TRADE_ADDPURSE_COMPLETE') {
                this.getPurse();
                this.doingPurse = false;
                this.alert(this.$('User.PurseAddComplete'), this.$t('System.Info'));
              }
              else {
                this.alert('Unexpected Response!', this.$t('System.Error'));
              }
            }
          });
        }
      }
    },
    newAvatar () {
      this.uploadingAvatar = true;
    },
  },
  mounted () {
    if (Cookies.get('loginAs')) {
      this.getUserInfo();
      this.getPurse();
    }
    else {
      this.goLogin();
    }
  }
}
</script>

<style scoped>
.userme {
  text-align: left;
  width: 90%;
  margin-left: 5%;
}
.myInfo {
  font-size: 25px;
  font-weight: bold;
  margin-bottom: 30px;
}
.infos {
  margin-top: 30px;
  font-size: 20px;
}
.edit {
  float: right;
}
.infoRight {
  float: right;
}
.infoItem {
  margin-bottom: 20px;
}
.editable {
  cursor: pointer;
}
.uneditable {
  cursor: not-allowed;
}
.buttons {
  float: right;
  margin-bottom: 20px;
  margin-top: 30px;
}
.aButton {
  margin-left: 15px;
  font-size: 18px;
}
.dialog ::v-deep .el-dialog {
  border-radius: 25px;
}
.inputBox {
  margin-bottom: 10px;
}
.select {
  text-align: left;
  margin-bottom: 15px;
}
.selectBox {
  margin-left: 20px;
}
.doingPurse .label {
  margin-top: 20px;
  font-size: 16px;
}
.doingPurse .button {
  text-align: center;
  margin-top: 25px;
}
.avatarUploader {
  text-align: center;
}
.avatarUploader .avatarWarn {
  font-size: 18px;
  margin-top: 15px;
}
.avatarUploader .uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatarUploader .uploader .el-upload:hover {
  border-color: #409eff;
}
.avatarUploader .uploader .avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
  border: 1px solid gray;
  border-radius: 15px;
}
.avatarUploader .uploader .avatarImage {
  width: 178px;
  height: 178px;
  display: block;
  border: 1px solid gray;
  border-radius: 15px;
}
</style>